import {ViewChild, Component, OnInit, AfterViewInit, ElementRef} from '@angular/core';
import {MatPaginator, MatSnackBar, MatSort,MatDialog,MatDialogRef} from '@angular/material';
import { merge } from 'rxjs/observable/merge';
import {Item} from "../../model/item";
import {Dataserviceserver, ItemDataSource} from "./dataserviceserver";
import {tap} from "rxjs/operators";
import {animate, state, style, transition, trigger} from '@angular/animations';
import { ConfirmDialogComponent } from '../../shared/confirm-dialog/confirm-dialog.component';


@Component({
  selector: 'app-datatable-server',
  styleUrls: ['./datatableserver.component.scss'],
  templateUrl: './datatableserver.component.html',
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],

})
export class DatatableserverComponent implements AfterViewInit,OnInit {

  displayedColumns: string[] = ['id', 'custno', 'itemno', 'itemDescription',"actions"];
  dataSource: ItemDataSource;
  selectedFile:File=null;
  expandedElement: Item | null;
  confirmationDialogRef: MatDialogRef<ConfirmDialogComponent>;

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;


 public constructor(private dataService: Dataserviceserver, private _snackBar: MatSnackBar, private dialog: MatDialog) {
  }

  ngOnInit() {
    this.dataSource = new ItemDataSource(this.dataService);
    this.reloadData()

    this.openSnackBar("Demo of angular material table with server side pagination & sorting.","Close")

 }

  reloadData() {
    // set paginator page size
    this.paginator.pageIndex = 0;
    this.paginator.pageSize = 5;
    this.sort.active = 'id';
    this.sort.direction = 'asc';
    this.dataSource.loadItems(this.paginator.pageIndex, this.paginator.pageSize,'id','asc');
  }

  //called after view is initialized and created.
  ngAfterViewInit() {

    // reset the paginator after sorting
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);

    this.dataSource.counter$
      .pipe(
        tap((count) => {
          this.paginator.length = count;
        })
      )
      .subscribe();

    //use merge operator to combine the output of multiple observables so they act like one.
    // tap can perform side effects with observed data but does not modify the stream in any way.
    //tap returns identical observable as the source.
    //paginator has a observable based API and it exposes page observable and it emits new value
    //whenever user clicks pagination buttons.
    merge(this.paginator.page, this.sort.sortChange)
      .pipe(
        tap(() => this.dataSource.loadItems(this.paginator.pageIndex, this.paginator.pageSize, this.sort.active, this.sort.direction))
      )
      .subscribe();


  }//end ngAfterViewInit


  openConfirmationDialog(item: Item) {
    this.confirmationDialogRef = this.dialog.open(ConfirmDialogComponent, {
      disableClose: false
    });
    this.confirmationDialogRef.componentInstance.confirmMessage = "Are you sure you want to delete this record ?"

    this.confirmationDialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.delete(item)
      }
      this.confirmationDialogRef = null;
    });
  }


  delete(item: Item): void {
    this.dataService.delete(item)
      .subscribe( data => {
        this.reloadData()

      });
  }

  onFileSelected(fileList:FileList){
      this.selectedFile = <File>fileList[0];
  }

  onUpload(){
    if(this.selectedFile != null){
      this.dataService.uploadFile(this.selectedFile)
        .subscribe( data => {
          this.reloadData()
        });
    }
  }

  openSnackBar(message: string, action: string) {
    this._snackBar.open(message, action, {
      duration: 2000,
      verticalPosition: 'top'
    });
  }




}



