import {Component  } from '@angular/core';
import {FormBuilder,  Validators} from "@angular/forms";
import {Item} from "../../../model/item";
import {DataService} from "../dataservice";
import {BehaviorSubject} from "rxjs";
import {MatDialogRef, MatSnackBar} from "@angular/material";
import {finalize} from "rxjs/operators";

@Component({
  selector: 'app-add-datatable',
  templateUrl: './add-datatable.component.html',
  styleUrls: ['./add-datatable.component.scss']
})
export class AddDatatableComponent {

  service: DataService;

  //adding progress indicator.
  private addingSubject = new BehaviorSubject<boolean>(false);
  public adding$ = this.addingSubject.asObservable();

  constructor(private fb: FormBuilder,private dialogRef:MatDialogRef<AddDatatableComponent>,private _snackBar: MatSnackBar) {}

  addItemForm = this.fb.group({
    itemNo: ['', [Validators.required]],
    itemDesc: ['', [Validators.required]],
    custno: ['', [Validators.required]]
  });


  closeDialog(){
    this.dialogRef.close()
  }


  save(){
    this.addingSubject.next(true);

    let item = new Item();
    item.custno=this.addItemForm.get('custno').value
    item.itemDescription=this.addItemForm.get('itemDesc').value
    item.itemno=this.addItemForm.get('itemNo').value

    this.service.createItem(item)
      .pipe(
        finalize(() => { //if API call succeeds or fails set the saving indicator to false .
          this.addingSubject.next(false);
          this.closeDialog()
        })
      )
      .subscribe( data => {
        this.openSnackBar("Item added successfully.","Close")
      });
  }


  openSnackBar(message: string, action: string) {
    this._snackBar.open(message, action, {
      duration: 2000,
      verticalPosition: 'top'
    });
  }

}
