import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';


import {HomeComponent} from '../pages/home/home.component';


const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'datatableserver', loadChildren: () => import('../pages/datatableserver/datatableserver.module').then(m => m.DatatableserverModule)},
  {path: 'datatable', loadChildren: () => import('../pages/datatableclient/datatable.module').then(m => m.DatatableModule)}


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRouters {}
